import { Injectable } from '@angular/core';
import { SchemaManagerService, LcpService } from '@qdp/common';
import { Observable } from 'rxjs/Observable';
import { Subject } from 'rxjs/Subject';

@Injectable()
export class SchemaService {
  schemaInfo = new Subject<any>();
  colList = new Subject<any>();
  pageInfoChange = new Subject<any>();
  operationChange = new Subject<any>();

  constructor(private schemaManager: SchemaManagerService, private lcpService: LcpService) {
    this.schemaManager.schemaInfoChange.subscribe(value => {
      this.schemaInfo.next(value);
    });
    this.schemaManager.colList.subscribe(value => {
      this.colList.next(value);
    });
    this.lcpService.pageInfoChange.subscribe(value => {
      this.pageInfoChange.next(value);
    });
    this.schemaManager.operationChange.subscribe(value => {
      this.operationChange.next(value);
    });
  }

  getSchemaList(queryId: string, userId: string, organizationId: string, queryRelativeUrl: any, groupType?: any): Observable<any> {
    if (groupType) {
      return this.schemaManager.getSchemaListByGroupType(queryId, userId, organizationId, groupType, queryRelativeUrl);
    } else {
      return this.schemaManager.getSchemaList(queryId, userId, organizationId, queryRelativeUrl);
    }
  }

  getSchema(id: string, queryId: string, queryRelativeUrl: any, groupType?: any, organizationId?: any): Observable<any> {
    return this.schemaManager.getSchema(id, queryId, queryRelativeUrl, groupType, organizationId);
  }

  saveSchema(schema: any, queryRelativeUrl: any): Observable<any> {
    return this.schemaManager.saveSchema(schema, queryRelativeUrl);
  }

  deleteSchema(id: string, queryId: string, queryRelativeUrl: any, groupType?: any, organizationId?: any): Observable<any> {
    return this.schemaManager.deleteSchema(id, queryId, queryRelativeUrl, groupType, organizationId);
  }

  getSpreadTemplate(id: string, queryId: string, dynamicCols: any, queryRelativeUrl: any, sheme?: any, frozenCols?: any, groupType?: any): Observable<any> {
    return this.schemaManager.getSpreadTemplate(id, queryId, dynamicCols, queryRelativeUrl, sheme, frozenCols, groupType);
  }

  getData(param: any, queryRelativeUrl: any): Observable<any> {
    return this.lcpService.getData(param, queryRelativeUrl);
  }

  getCol(params: any, queryRelativeUrl: any): Observable<any> {
    return this.lcpService.getCol(params, queryRelativeUrl);
  }

  getOperation() {
    return this.schemaManager.getOperation();
  }
}
