import { Component, OnInit, OnChanges, SimpleChanges, ViewEncapsulation, Input, Output, EventEmitter, ViewChild, Injector } from '@angular/core';
import { FarrisTabsComponent } from '@farris/ui-tabs';
import { LocalizeService } from '@qdp/localize';
import { TitleEditorComponent } from './title-editor/title-editor.component';
import { ColumnEditorComponent } from './column-editor/column-editor.component';
import { StyleEditorComponent } from './style-editor/style-editor.component';
import { OtherEditorComponent } from './other-editor/other-editor.component';

@Component({
  selector: 'schema-editor',
  templateUrl: './schema-editor.component.html',
  styleUrls: ['./schema-editor.component.css'],
  encapsulation: ViewEncapsulation.None
})
export class SchemaEditorComponent implements OnInit, OnChanges {
  @Input() currSchema: any;
  @Input() colList: any[];
  @Input() controlType: string;
  editorParams: any;
  @Output() tabsSelectChange = new EventEmitter<any>();
  @ViewChild(TitleEditorComponent) titleOption: TitleEditorComponent;
  @ViewChild(ColumnEditorComponent) columnOption: ColumnEditorComponent;
  @ViewChild(StyleEditorComponent) styleOption: StyleEditorComponent;
  @ViewChild(OtherEditorComponent) otherOption: OtherEditorComponent;
  @ViewChild(FarrisTabsComponent) tabs: FarrisTabsComponent;
  localizeService: LocalizeService;

  constructor(private injector: Injector) {
    this.localizeService = this.injector.get(LocalizeService);
  }

  ngOnInit() {
  }

  ngOnChanges(changes: SimpleChanges) {
  }

  handleTabSelect(data: any) {
    const option = this.getOption();
    if (data.nextId === 'column') {
      this.columnOption.handleInfoChanged(0);
    }
    this.tabsSelectChange.emit(option);
  }

  getOption() {
    const option = {};
    if (this.titleOption) {
      option['titleOption'] = this.titleOption.getTitleOption();
    }
    if (this.columnOption) {
      option['columnOption'] = this.columnOption.getColumnOption();
    }
    if (this.styleOption) {
      option['styleOption'] = this.styleOption.getStyleOption();
    }
    if (this.otherOption) {
      option['otherOption'] = this.otherOption.getOtherOption();
    }

    return option;
  }
}
