import { Injectable } from '@angular/core';
import { Subject, Observable } from 'rxjs';
import { PrintIntegrationManagerService } from '@qdp/common';

@Injectable()
export class PrintIntegrationService {

  printIntegrationInfo = new Subject<any>();

  constructor(private printIntegrationManager: PrintIntegrationManagerService) {
    this.printIntegrationManager.printIntegrationInfoChange.subscribe(value => {
      this.printIntegrationInfo.next(value);
    });
  }

  getPrintIntegration(schemaId: string, queryId: string, queryRelativeUrl: any): Observable<any> {
    return this.printIntegrationManager.getPrintIntegration(schemaId, queryId, queryRelativeUrl);
  }

  savePrintIntegration(printIntegrationInfo: any, queryRelativeUrl: any): Observable<any> {
    return this.printIntegrationManager.savePrintIntegration(printIntegrationInfo, queryRelativeUrl);
  }

  deletPrintIntegration(schemaId: string, queryID: string, queryRelativeUrl: any): Observable<any> {
    return this.printIntegrationManager.deletePrintIntegration(schemaId, queryID, queryRelativeUrl);
  }
}
