import { Injectable } from '@angular/core';
import { Subject, Observable } from 'rxjs';
import { JointSearchManagerService, LcpService } from '@qdp/common';

@Injectable()
export class JointSearchService {

  jointSearchInfo = new Subject<any>();

  constructor(private jointSearchManager: JointSearchManagerService, private lcpService: LcpService) {
    this.jointSearchManager.jointsearchInfoChange.subscribe(value => {
      this.jointSearchInfo.next(value);
    });
  }

  getjointsearch(id: string, queryId: string, queryRelativeUrl: any): Observable<any> {
    return this.jointSearchManager.getjointsearch(id, queryId, queryRelativeUrl);
  }

  savejointsearch(jointSearchInfo: any, queryRelativeUrl: any): Observable<any> {
    return this.jointSearchManager.savejointsearch(jointSearchInfo, queryRelativeUrl);
  }

  deletejointsearch(formid: any, queryID: any, queryRelativeUrl: any): Observable<any> {
    return this.jointSearchManager.deletejointsearch(formid, queryID, queryRelativeUrl);
  }

  getLanguageList() {
    return this.jointSearchManager.getLanguageList();
  }
  
  getCol(params: any, queryRelativeUrl: any): Observable<any> {
    return this.lcpService.getCol(params, queryRelativeUrl);
  }
}
