import { Injectable } from '@angular/core';
import { Subject, Observable } from 'rxjs';
import { ParasMappingManagerService } from '@qdp/common';

@Injectable()
export class ParasMappingService {

  paramMappingInfo = new Subject<any>();

  constructor(private parasMappingManager: ParasMappingManagerService) {
    this.parasMappingManager.parasmappingInfoChange.subscribe(value => {
      this.paramMappingInfo.next(value);
    });
  }

  getParasMapping(id: string, queryId: string, queryRelativeUrl: any): Observable<any> {
    return this.parasMappingManager.getParasMapping(id, queryId, queryRelativeUrl);
  }

  saveParasMapping(paramMappingInfo: any, queryRelativeUrl: any): Observable<any> {
    return this.parasMappingManager.saveParasMapping(paramMappingInfo, queryRelativeUrl);
  }

  deleteParasMapping(datasourceID: string, queryID: string, queryRelativeUrl: any): Observable<any> {
    return this.parasMappingManager.deleteParasMapping(datasourceID, queryID, queryRelativeUrl);
  }
}
