import { Component, Input, OnInit, OnChanges, SimpleChanges, Output, EventEmitter, Injector } from '@angular/core';
import { findIndex } from 'lodash-es';
import { LocalizeService } from '@qdp/localize';

@Component({
  selector: 'charts-type',
  templateUrl: './charts-type.component.html',
  styleUrls: ['./charts-type.component.css']
})
export class ChartsTypeComponent implements OnInit, OnChanges {
  @Input() currSchema: any;
  chartsType: any = {
    series: {
      type: null
    }
  };
  chartsTypeData: any = [
    { name: 'è¯·éæ©', value: -1 },
    { name: 'æçº¿å¾', value: 'line' },
    { name: 'æ±ç¶å¾', value: 'bar' },
    { name: 'é¥¼ç¶å¾', value: 'pie' },
    { name: 'é¢ç§¯å¾', value: 'line1' },
    { name: 'æ£ç¹å¾', value: 'scatter' },
    { name: 'å°å¾æ£ç¹', value: 'effectScatter' },
    // { name: 'ä¸­å½å°å¾', value: 'map'},
    // ä¸­å½å°å¾å·²æ¯æã
  ];
  chartsTypeSelectValue: any = -1;
  @Output() chartTypeChange = new EventEmitter<any>();
  localizeService: LocalizeService;

  constructor(private injector: Injector) {
    this.localizeService = this.injector.get(LocalizeService);
    this.chartsTypeData = this.localizeService.getValue('ideCmp.chartsSchemaManager.chartsType.chartsTypeData');
  }

  ngOnInit() {
  }

  ngOnChanges(changes: SimpleChanges) {
    const self = this;
    for (const changeName in changes) {
      const newValue = changes[changeName].currentValue;
      if (newValue !== null && newValue !== void 0) {
        switch (changeName) {
          case 'currSchema':
            if (newValue.schemaValue && newValue.schemaValue.chartsType) {
              const str = JSON.stringify(newValue.schemaValue.chartsType);
              this.chartsType = JSON.parse(str);
              const index = findIndex(this.chartsTypeData, function (c: any) { return c.value === (self.chartsType.series.type || -1); });
              if (index >= 0) {
                this.chartsTypeSelectValue = this.chartsTypeData[index].value;
                this.valueChange(this.chartsTypeSelectValue); // æ´æ°ç¬¬äºä¸ªçé¢
              }
            }
            break;
        }
      }
    }
  }

  valueChange(value: any): void {
    this.chartsType.series.type = value.value || -1;
    this.chartTypeChange.emit(value.value);
  }

  getChartsTypeOption() {
    if (this.chartsTypeSelectValue !== -1) {
      this.chartsType.series.type = this.chartsTypeSelectValue;
      if (this.chartsType.series.type.indexOf('1') >= 0) {
        this.chartsType.series.areaStyle = {};
      } else {
        delete this.chartsType.series.areaStyle;
      }
    }
    return this.chartsType;
  }

}
